@charset "UTF-8";
body[data-controller=case-study] main h1 span.title {
  display: block;
  margin-top: 1rem;
  font-weight: 600;
  line-height: 1.2;
}
body[data-controller=case-study] ul.attributes {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin: 2rem 0 0;
  font-family: Avenir Next, Libre Franklin, sans-serif;
  font-size: 2.4rem;
  font-weight: 500;
  color: hsl(176,33%,30%);
  text-align: center;
  line-height: 1.4;
}
body[data-controller=case-study] ul.attributes li {
  padding: 0.2rem 0;
}
body[data-controller=case-study] h2.tagline {
  padding: 0 2rem;
  font-size: 2.4rem;
  color: hsl(0,0%,20%);
  text-align: center;
}
body[data-controller=case-study] p.summary {
  margin: 3rem 2rem;
  text-align: center;
}
body[data-controller=case-study] section.devices {
  margin-bottom: 2rem;
  overflow-x: hidden;
}
body[data-controller=case-study] section.devices img {
  width: 100%;
}
body[data-controller=case-study] section.logo {
  display: flex;
  justify-content: center;
  height: 5rem;
  margin: 6rem auto 4rem;
}
body[data-controller=case-study] section.logo img {
  height: 100%;
  max-width: 100%;
  object-fit: contain;
}
body[data-controller=case-study] section.screens figure[role=group]:only-child figcaption {
  display: none;
}
body[data-controller=case-study] section.Linode section.devices img,
body[data-controller=case-study] section.Vidyo section.devices img,
body[data-controller=case-study] section.Fitly section.devices img,
body[data-controller=case-study] section.hibu section.devices img {
  width: 142%;
  margin-left: -21%;
}
body[data-controller=case-study] section.Linode figure[role=group].callout figure {
  margin-bottom: 1rem;
}
body[data-controller=case-study] section.hibu figure.callout.concept section,
body[data-controller=case-study] section.hibu figure.callout.prototype section {
  display: flex;
  justify-content: space-between;
}
body[data-controller=case-study] section.hibu figure.callout.concept section figure:nth-child(1),
body[data-controller=case-study] section.hibu figure.callout.prototype section figure:nth-child(1) {
  flex: 0 0 35.3%;
  margin: 0;
}
body[data-controller=case-study] section.hibu figure.callout.concept section figure:nth-child(2),
body[data-controller=case-study] section.hibu figure.callout.prototype section figure:nth-child(2) {
  flex: 0 0 60%;
  margin: 0;
}
body[data-controller=case-study] section.Fitly figure.callout.pricing-cta figure:nth-child(1) {
  margin-top: 0;
  margin-bottom: 1rem;
}
body[data-controller=case-study] section.Fitly figure.callout.pricing-cta figure:nth-child(2) {
  margin: 0 auto 2rem;
}
body[data-controller=case-study] section.Vidyo section.screens figure[role=group]:nth-child(4) figure {
  border: none;
}
body[data-controller=case-study] section.screens.group {
  padding-top: 2rem;
  background: hsl(175,55%,76%);
}
body[data-controller=case-study] section.screens.group figure[role=group] figure {
  border: 0.1rem solid hsl(175,55%,66%);
}
@media only screen and (min-width: 768px) {
  body[data-controller=case-study] main h1 span.title {
    display: initial;
  }
  body[data-controller=case-study] ul.attributes {
    text-align: left;
    margin-top: 0.8rem;
  }
  body[data-controller=case-study] ul.attributes li {
    display: flex;
  }
  body[data-controller=case-study] ul.attributes li:before {
    content: "•";
    display: block;
    padding: 0.6rem 1rem 0 0;
    color: hsl(175,55%,76%);
  }
  body[data-controller=case-study] section.logo {
    height: 5.6rem;
  }
  body[data-controller=case-study] section.logo img {
    max-width: 50%;
  }
  body[data-controller=case-study] h2.tagline {
    padding: 0;
    max-width: 50rem;
    margin: auto;
  }
  body[data-controller=case-study] p.summary {
    max-width: 64rem;
    margin: 3rem auto 6rem;
    font-size: 2rem;
  }
  body[data-controller=case-study] section.devices {
    width: auto;
    margin-bottom: 4rem;
    margin-left: 0;
    padding: 0 4rem;
  }
  body[data-controller=case-study] section.Linode section.devices img,
body[data-controller=case-study] section.Vidyo section.devices img,
body[data-controller=case-study] section.hibu section.devices img,
body[data-controller=case-study] section.Fitly section.devices img {
    margin: 0 !important;
    width: 100% !important;
  }
  body[data-controller=case-study] section.Abercrombie-and-Fitch section.devices,
body[data-controller=case-study] section.OncoTracker section.devices {
    display: flex;
    justify-content: center;
  }
  body[data-controller=case-study] section.Abercrombie-and-Fitch section.devices img,
body[data-controller=case-study] section.OncoTracker section.devices img {
    width: auto;
    height: 27rem;
  }
  body[data-controller=case-study] section.Linode figure[role=group]:not(.screens) section {
    display: flex;
    justify-content: space-between;
  }
  body[data-controller=case-study] section.Linode figure[role=group]:not(.screens) section figure img {
    height: 24rem;
  }
  body[data-controller=case-study] section.Linode figure[role=group]:not(.screens).usability-testing img {
    height: 12.6rem;
  }
  body[data-controller=case-study] section.Linode figure[role=group].callout figure {
    margin-bottom: 0;
  }
  body[data-controller=case-study] figure.callout.zip-code img {
    height: 30rem;
  }
  body[data-controller=case-study] figure.callout.kitchen-view img {
    height: 48rem;
  }
  body[data-controller=case-study] figure.callout.pricing-cta figure {
    margin-bottom: 2rem;
  }
  body[data-controller=case-study] figure.callout.pricing-cta img {
    height: 24rem;
  }
  body[data-controller=case-study] section.hibu figure.callout.concept section,
body[data-controller=case-study] section.hibu figure.callout.prototype section {
    justify-content: center;
  }
  body[data-controller=case-study] section.hibu figure.callout.concept section figure:nth-child(1),
body[data-controller=case-study] section.hibu figure.callout.concept section figure:nth-child(2),
body[data-controller=case-study] section.hibu figure.callout.prototype section figure:nth-child(1),
body[data-controller=case-study] section.hibu figure.callout.prototype section figure:nth-child(2) {
    flex-basis: auto;
    margin: 0 1rem;
  }
  body[data-controller=case-study] main nav {
    position: initial;
    justify-content: center;
    margin: 6rem 0;
  }
}
@media only screen and (min-width: 840px) {
  body[data-controller=case-study] section.screens.group {
    padding-bottom: 2rem;
  }
  body[data-controller=case-study] section.screens.group figure[role=group] figure {
    margin-bottom: 0;
  }
}
@media only screen and (min-width: 1024px) {
  body[data-controller=case-study] section.case-study {
    max-width: none;
  }
  body[data-controller=case-study] section.intro {
    max-width: 128rem;
    margin: auto;
  }
  body[data-controller=case-study] section.screens.group {
    padding-top: 2rem;
  }
  body[data-controller=case-study] section.Linode figure[role=group]:not(.screens) section figure img {
    height: 28rem;
  }
  body[data-controller=case-study] section.Linode figure[role=group]:not(.screens).usability-testing img {
    height: 14.6rem;
  }
}
@media only screen and (min-width: 1280px) {
  body[data-controller=case-study] section.intro {
    padding: 0 4rem;
    background: none;
  }
  body[data-controller=case-study] ul.attributes {
    flex-direction: row;
  }
  body[data-controller=case-study] ul.attributes li {
    margin-right: 1rem;
  }
  body[data-controller=case-study] ul.attributes li:first-child:before {
    display: none;
  }
  body[data-controller=case-study] section.about {
    display: grid;
    grid-template: auto/37% 63%;
    max-width: 120rem;
    margin: 8rem auto 6rem;
  }
  body[data-controller=case-study] section.logo {
    height: 5rem;
    justify-content: left;
    margin: 0 0 2rem;
  }
  body[data-controller=case-study] section.logo img {
    max-width: 40rem;
  }
  body[data-controller=case-study] h2.tagline {
    grid-column: 1;
    font-size: 2.2rem;
    text-align: left;
  }
  body[data-controller=case-study] p.summary {
    grid-column: 1;
    margin-bottom: 0;
    font-size: 1.8rem;
    text-align: left;
  }
  body[data-controller=case-study] section.devices {
    grid-row: 1/4;
    grid-column: 2;
    margin: 2rem 0 0 4rem;
    padding: 0;
  }
  body[data-controller=case-study] section.screens.group {
    padding: 0;
  }
  body[data-controller=case-study] section.screens.group figure[role=group] section {
    padding: 3rem;
  }
  body[data-controller=case-study] section.screens.group figure[role=group] figure {
    flex-basis: 28rem;
    margin: 0 3rem 0 0;
  }
}