body[data-controller=contact] li.nope {
  position: absolute;
  top: -100%;
}
@media only screen and (max-width: 767px) {
  body[data-controller=contact] main > h2 {
    margin-bottom: 2rem;
  }
}
body[data-controller=contact] ul.social {
  display: flex;
  justify-content: space-around;
  max-width: 26rem;
  margin: 0 auto 6rem;
  list-style: none;
}
body[data-controller=contact] ul.social a {
  position: relative;
  display: block;
  margin: 0;
  padding: 0 0 0 6rem;
  width: 6rem;
  height: 6rem;
  text-indent: 110%;
  overflow: hidden;
  text-decoration: none;
  background: url("/images/icons.svg") no-repeat;
  background-size: cover;
}
body[data-controller=contact] ul.social li.github a {
  background-position: -8rem 0;
}
body[data-controller=contact] ul.social li.twitter a {
  background-position: -16rem 0;
}
body[data-controller=contact] form {
  padding: 0 2rem 6rem;
}
body[data-controller=contact] form label {
  margin-bottom: 0.6rem;
  line-height: 1;
}
body[data-controller=contact] form p.label-note {
  margin: 0.4rem 0 0.6rem;
  font-size: 90%;
}
body[data-controller=contact] form input:not([type=submit]),
body[data-controller=contact] form select,
body[data-controller=contact] form textarea {
  width: 100%;
  min-height: 4.5rem;
  padding: 1.2rem;
  font-family: "Libre Franklin", sans-serif;
  font-size: 1.6rem;
  border: 0.1rem solid #cccccc;
  border-radius: 0.4rem;
  transition: border-color 0.2s, background-color 0.2s;
}
body[data-controller=contact] form input:not([type=submit]):focus,
body[data-controller=contact] form select:focus,
body[data-controller=contact] form textarea:focus {
  outline: none;
  border-color: hsl(2,86%,53%);
}
body[data-controller=contact] form textarea {
  height: 100%;
  line-height: 1.5;
}
body[data-controller=contact] form ul.data {
  margin: 0;
}
body[data-controller=contact] form ul.data > li {
  margin: 0.6rem 0 2.4rem;
  list-style: none;
}
body[data-controller=contact] form ul.data > li:last-child {
  margin-bottom: 0.6rem;
}
body[data-controller=contact] form ul.data > li.text label, body[data-controller=contact] form ul.data > li.textarea label, body[data-controller=contact] form ul.data > li.select label {
  display: block;
}
body[data-controller=contact] form ul.data > li.select {
  position: relative;
}
body[data-controller=contact] form ul.data > li.select::after {
  content: "";
  display: block;
  width: 1.6rem;
  height: 2rem;
  position: absolute;
  right: 0.8rem;
  bottom: 1.2rem;
  background: url(../images/icons.svg) -24rem no-repeat;
  background-size: cover;
}
body[data-controller=contact] form ul.actions {
  margin: 4rem 0;
  list-style-type: none;
}
body[data-controller=contact] form ul.actions li {
  display: flex;
  justify-content: center;
}
@media only screen and (min-width: 425px) {
  body[data-controller=contact] form {
    max-width: 40rem;
    margin: auto;
  }
}
@media only screen and (min-width: 768px) {
  body[data-controller=contact] main h2 {
    text-align: center;
  }
}
@media only screen and (min-width: 1024px) {
  body[data-controller=contact] main {
    max-width: 80rem;
    margin: auto;
  }
  body[data-controller=contact] main h2 {
    text-align: left;
  }
  body[data-controller=contact] ul.social {
    justify-content: flex-start;
    margin-bottom: 2rem;
    margin-left: 0;
    padding: 0 4rem;
  }
  body[data-controller=contact] ul.social li {
    margin-right: 2.8rem;
  }
  body[data-controller=contact] form {
    max-width: 72rem;
    padding-right: 0;
    padding-left: 0;
  }
  body[data-controller=contact] form ul.data {
    display: grid;
    grid-template: auto/50% 50%;
    row-gap: 2rem;
    column-gap: 4rem;
  }
  body[data-controller=contact] form ul.data li {
    margin: 0;
  }
  body[data-controller=contact] form ul.data li.email, body[data-controller=contact] form ul.data li.subject {
    grid-column: 1;
  }
  body[data-controller=contact] form ul.data li.message {
    grid-row: 1/4;
    grid-column: 2;
    margin-bottom: 0;
  }
  body[data-controller=contact] form ul.data li.message textarea {
    height: 23rem;
  }
}
body[data-controller=contact][data-action=confirmation] h1,
body[data-controller=contact][data-action=confirmation] section.content p {
  text-align: center;
}