body[data-controller=work-history] main > h2 {
  margin-bottom: 0;
}
body[data-controller=work-history] section.resume ul,
body[data-controller=work-history] section.competencies ul {
  margin-top: 0;
  margin-left: 0;
  list-style: none;
}
body[data-controller=work-history] section.resume li,
body[data-controller=work-history] section.competencies li {
  margin-bottom: 1.4rem;
  padding-left: 1.5rem;
  border-left: 0.2rem solid hsl(0,0%,90%);
}
body[data-controller=work-history] section.resume ul {
  display: flex;
  justify-content: center;
}
body[data-controller=work-history] section.resume li {
  margin-right: 1.5rem;
}
body[data-controller=work-history] section.resume li:first-child {
  padding-left: 0;
  border-left: none;
}
body[data-controller=work-history] section.resume li:last-child {
  margin-right: 0;
}
body[data-controller=work-history] section.employer {
  margin: 2rem 0 6rem;
}
body[data-controller=work-history] section.employer h3,
body[data-controller=work-history] section.employer h4 {
  padding: 0;
  text-align: left;
}
body[data-controller=work-history] section.employer p.dates {
  font-size: 1.6rem;
  color: #808080;
}
body[data-controller=work-history] section.employer p.case-study {
  font-size: 1.8rem;
}
body[data-controller=work-history] section.employer p.case-study a {
  display: block;
  margin-top: 0.4rem;
}
@media only screen and (min-width: 768px) {
  body[data-controller=work-history] section.resume li,
body[data-controller=work-history] section.competencies li {
    margin-right: 2rem;
    padding-left: 2rem;
  }
  body[data-controller=work-history] section.resume ul {
    justify-content: flex-start;
  }
  body[data-controller=work-history] section.employer p.case-study a {
    display: initial;
  }
}
@media only screen and (min-width: 1280px) {
  body[data-controller=work-history] main {
    display: grid;
    grid-template: auto auto auto/40% 60%;
    max-width: 128rem;
    margin: auto;
  }
  body[data-controller=work-history] main > h2 {
    margin-bottom: 0;
  }
  body[data-controller=work-history] section.intro {
    grid-column: 1/3;
  }
  body[data-controller=work-history] section.resume {
    margin: 6rem 0 0;
  }
  body[data-controller=work-history] section.work-history {
    grid-row: 2/5;
    grid-column: 2;
    overflow: hidden;
  }
  body[data-controller=work-history] section.work-history h2 {
    position: absolute;
    top: -5rem;
  }
  body[data-controller=work-history] section.work-history section.employer:first-of-type {
    margin-top: 0;
  }
  body[data-controller=work-history] section.resume,
body[data-controller=work-history] section.competencies {
    font-size: 1.8rem;
  }
  body[data-controller=work-history] section.resume h2,
body[data-controller=work-history] section.competencies h2 {
    font-size: 2.4rem;
  }
}
@media only screen and (min-width: 1440px) {
  body[data-controller=work-history] section.resume {
    margin: 10rem 0 0;
  }
  body[data-controller=work-history] section.competencies {
    margin-top: 6rem;
  }
}