body[data-controller=portfolio] section.documents ul {
  margin: 2rem auto 0;
  color: #cccccc;
}
body[data-controller=portfolio] section.documents li {
  margin: 0 0 0.8rem 2rem;
}
body[data-controller=portfolio] section.screens figure[role=group].Vidyo:nth-child(5) figure {
  border: none;
}
@media only screen and (min-width: 768px) {
  body[data-controller=portfolio] section.documents p,
body[data-controller=portfolio] section.documents ul,
body[data-controller=portfolio] section.all-screens p {
    max-width: 80rem;
    margin-right: auto;
    margin-left: auto;
  }
  body[data-controller=portfolio] section.documents ul {
    margin-top: 4rem;
    display: block;
    column-count: 2;
    column-gap: 2rem;
  }
}