@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,400;0,500;1,400&family=Raleway:wght@500;600;700;800;900&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 1;
}

body {
  word-wrap: break-word;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-weight: 900;
  line-height: 1.4;
}

h1 {
  font-size: 140%;
}

h2 {
  font-size: 130%;
}

h3 {
  font-size: 105%;
}

h4, h5, h6 {
  font-size: 100%;
}

ul,
ol {
  margin: 2.6rem 0 2.6rem 2rem;
}

li {
  margin-bottom: 0.8rem;
  line-height: 1.5;
}

dl {
  display: flex;
  flex-wrap: wrap;
  margin: 0 1.2rem 1.8rem;
  line-height: 1.5;
}

dt {
  flex: 0 0 30%;
}
dt::after {
  content: ":";
}

dd {
  flex: 0 0 70%;
  word-break: break-word;
}

nav ul {
  margin: 0;
  list-style: none;
}

p {
  margin: 0 0 1rem;
  line-height: 1.5;
}
@media only screen and (min-width: 768px) {
  p {
    margin-bottom: 2rem;
  }
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  display: block;
  -ms-interpolation-mode: bicubic;
}

q::before,
q::after {
  content: "";
}

input,
select,
textarea {
  appearance: none;
  border: none;
  border-radius: 0;
  background: none;
}

input:not([type=checkbox]),
input:not([type=radio]) {
  line-height: 1;
}