@charset "UTF-8";
body[data-controller=index] {
  background: hsl(175,55%,76%);
}
body[data-controller=index] header {
  background: hsl(175,55%,76%);
}
body[data-controller=index] main h1, body[data-controller=index] main h2 {
  color: white;
  text-shadow: 0 1px 3px hsl(175,42%,45%);
}
body[data-controller=index] main h2 {
  font-weight: 800;
}
body[data-controller=index] section.intro {
  background: hsl(175,55%,70%);
}
body[data-controller=index] section.intro p {
  color: hsl(176,33%,30%);
}
body[data-controller=index] section.case-studies h2 {
  padding-bottom: 0;
}
body[data-controller=index] section.screens {
  margin: 3rem 0 2rem;
}
body[data-controller=index] section.screens figure[role=group] figure {
  border: 0.1rem solid hsl(175,55%,66%);
}
body[data-controller=index] section.endorsements {
  background: hsl(175,55%,70%);
  padding-bottom: 8rem;
}
body[data-controller=index] blockquote {
  position: relative;
  padding: 2rem 2.5rem 4rem;
}
body[data-controller=index] blockquote:before {
  content: "“";
}
body[data-controller=index] blockquote:after {
  content: "”";
}
body[data-controller=index] blockquote:before, body[data-controller=index] blockquote:after {
  display: block;
  position: absolute;
  z-index: 2;
  height: 3rem;
  font-family: Raleway, sans-serif;
  font-weight: 800;
  font-size: 6.4rem;
  color: white;
  text-shadow: 0 2px 4px hsl(175,22%,55%);
}
body[data-controller=index] blockquote:before {
  top: 0.8rem;
  left: 1.4rem;
}
body[data-controller=index] blockquote:after {
  right: 1.4rem;
  bottom: 3rem;
}
body[data-controller=index] blockquote p {
  position: relative;
  z-index: 1;
  margin: 0;
  padding: 2.2rem 3.2rem;
  color: #444;
  background: hsl(175,100%,95%);
  border-radius: 1rem;
}
@media only screen and (min-width: 768px) {
  body[data-controller=index] section.case-studies {
    margin-bottom: 4rem;
  }
  body[data-controller=index] section.screens {
    margin: 4rem 0 1rem;
  }
  body[data-controller=index] section.screens figure[role=group] section {
    padding-left: 4rem;
    padding-right: 4rem;
  }
  body[data-controller=index] section.screens figure[role=group] section:after {
    flex-basis: 4rem;
  }
  body[data-controller=index] section.quotes {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    margin: 0 2.4rem;
  }
}
@media only screen and (min-width: 1024px) {
  body[data-controller=index] section.case-studies {
    margin: 6rem auto 8rem;
  }
  body[data-controller=index] section.endorsements {
    background: none;
  }
  body[data-controller=index] blockquote {
    flex: 0 0 50%;
  }
}
@media only screen and (min-width: 1440px) {
  body[data-controller=index] main > section {
    margin-bottom: 8rem;
  }
}

@media only screen and (min-width: 768px) {
  html.js body[data-controller=index] header {
    background: hsla(175,55%,70%,0);
  }
  html.js body[data-controller=index].fixed-header header {
    background: hsla(175,55%,65%,0.75);
  }
}