body[data-controller=case-studies] article {
  margin: 6rem 0;
}
body[data-controller=case-studies] article p {
  margin: 3rem 2rem 1rem;
  text-align: center;
}
body[data-controller=case-studies] section.logo {
  display: flex;
  justify-content: center;
  margin: 0 2rem;
}
body[data-controller=case-studies] section.logo a {
  display: inline-block;
  height: 5rem;
}
body[data-controller=case-studies] section.logo a img {
  height: 100%;
  max-width: 40rem;
  object-fit: contain;
}
body[data-controller=case-studies] section.screens.group {
  margin: 3rem 0 2rem;
}
@media only screen and (min-width: 768px) {
  body[data-controller=case-studies] article {
    margin-bottom: 10rem;
  }
  body[data-controller=case-studies] article p {
    margin: 3rem 4rem 0;
    font-size: 2rem;
    text-align: left;
  }
  body[data-controller=case-studies] section.logo {
    justify-content: flex-start;
    margin: 0 4rem;
  }
  body[data-controller=case-studies] section.screens.group {
    margin-bottom: 0;
  }
}
@media only screen and (min-width: 1024px) {
  body[data-controller=case-studies] article {
    margin: 8rem auto;
  }
  body[data-controller=case-studies] article nav {
    top: 0;
  }
}